<script>
  import { todoList } from "../store";
  export let todo;

  const remove = () => {
    $todoList = $todoList.filter((t) => t.pk !== todo.pk);
  };

  const toggleCheck = () => {
    todo.checked = !todo.checked;
  };
</script>

<style lang="scss">/* Variables */
/* End: Variables */
/* End: Mixins */
.todo {
  padding: 8px;
  background-color: white;
  border: 1px solid #e5e5ea;
  color: #3a3a3c;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: row;
  flex-wrap: wrap;
  border-radius: 4px; }
  .todo.done {
    background-color: #e5e5ea; }
  .todo:not(:last-child) {
    margin-bottom: 12px; }
  .todo .check {
    width: 24px;
    height: 24px;
    background-color: white;
    border: 1px solid #e5e5ea;
    margin-right: 12px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap; }
    .todo .check .checked-box {
      background-color: #aeaeb2;
      width: 12px;
      height: 12px; }
  .todo .info .name {
    font-size: 18px; }
    .todo .info .name.done {
      color: #aeaeb2;
      text-decoration: line-through; }
  .todo .info .created {
    color: #aeaeb2;
    font-size: 14px; }
  .todo .delete-button {
    display: none; }
  .todo:hover {
    background-color: #f2f2f7; }
    .todo:hover .delete-button {
      display: block;
      background-color: #ff3b30;
      color: #f2f2f7;
      margin: 0 8px 0 auto;
      align-self: center; }
</style>

<div class="todo" class:done={todo.checked}>
  <div class="check" on:click={toggleCheck}>
    {#if todo.checked}
      <div class="checked-box" />
    {/if}
  </div>
  <div class="info">
    <div class="name" class:done={todo.checked}>{todo.name}</div>
    <div class="created">
      {`${todo.created.getFullYear()}년 ${todo.created.getMonth() + 1}월 ${todo.created.getDate()}일 ${todo.created.getHours()}시 ${todo.created.getMinutes()}분 ${todo.created.getSeconds()}초`}
    </div>
  </div>
  <button on:click={remove} class="delete-button">삭제</button>
</div>
