<script>
  import { todoList } from "../store";

  let todo = "";

  const addTodo = () => {
    if (todo) {
      const newTodo = {
        pk: $todoList[0] ? $todoList[0].pk + 1 : 1,
        name: todo,
        created: new Date(),
        checked: false,
      };
      $todoList = [newTodo, ...$todoList];
      todo = "";
      return;
    }
    alert("한 글자 이상 입력해야 합니다.");
  };
</script>

<style lang="scss">/* Variables */
/* End: Variables */
/* End: Mixins */
.input-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap;
  margin-bottom: 16px; }
  .input-wrapper input {
    width: 82%;
    height: 50px;
    border: 1px solid #e5e5ea;
    border-radius: 4px; }
  .input-wrapper button {
    height: 50px;
    width: 13%;
    background-color: #007aff;
    color: #f2f2f7;
    border-radius: 4px; }
</style>

<div class="input-wrapper">
  <input type="text" bind:value={todo} placeholder="할 일을 입력해주세요." />
  <button on:click={addTodo}>추가</button>
</div>
